.fancy-button {
  position: relative;
  cursor: pointer;
  overflow: hidden;
  color: white;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.fancy-button .flair {
  position: absolute;
  background-color: white;
  aspect-ratio: 1;
  width: 200%;
  pointer-events: none;
  border-radius: 100%;
  will-change: transform;
  z-index: 0;
}
.fancy-button .label {
  font-family: inherit;
  z-index: 0;
  transition: color 0.2s;
}
.fancy-button:hover {
  color: black;
}